@import url('https://fonts.googleapis.com/css2?family=Koulen&display=swap');



.maincontainer-home{
    height: 100vh;
    background-color: rgb(16, 38, 56);

}

.container{
    height: 100vh;
    width: 100%;
}

.container.first{
    padding-top: 65px;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.container.first .content{
    width: 60%;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #e9b571;
    /* font-family: 'Koulen', cursive; */
    /* font-size: 100pt; */
    font-weight: 600;
    /* -webkit-text-stroke: 1px #FFAC41;  */
}

.pages-wrap{
    width: 300%;
    height: 100vh;
    display: flex;
    flex-wrap: nowrap;
}

.pages-wrap .page{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: 10px;
    background-color: darkkhaki;
}













